html, body {
	font-family: $default-font;
	font-weight: 400;
	font-style: normal;
	font-size: 16px;
	overflow-x: hidden;
	line-height: 1.3;
	position: relative;
	min-width: 320px; }

* {
	margin: 0;
	padding: 0; }

ul {
	margin: 0;
	padding: 0;
	list-style-type: none; }

a,
a:focus,
a:hover {
	outline: none;
	text-decoration: none; }
