.modal .form .button { display: inline-block; z-index: 1; position: relative; font-size: 14px; text-transform: uppercase; font-weight: 700; color: #ffffff; background-image: linear-gradient(to right, #7474db 0%, #39dbf7 100%); line-height: 1; padding: 20px 58px 20px 35px; border-radius: 5px; transition: all .5s; box-shadow: rgba(116, 116, 219, 0.3) -10px 10px 40px 3px, rgba(57, 219, 247, 0.3) 10px 10px 40px 3px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; max-width: none; max-height: 60px; }

